import React, { useState, useEffect } from "react";
import Poster from "../components/Poster";
import Banner from "../components/Banner";
import Input from "../components/Input";

const axios = require("axios");
const jikan = require("jikanjs");

function Library() {
  const [posters, setPosters] = useState([]);

  const addPoster = (title) => {
    jikan.search("anime", title).then((res) => {
      console.log(res.results[0]);
      const anime = res.results[0];
      const name = anime.title;
      const href = anime.url;
      const img = anime.image_url;
      console.log(name, href, img);
      setPosters([{ name, img, href }, ...posters]);
      axios.post("https://mysql-animes.herokuapp.com/add", { name, img, href });
    });
  };

  const deletePoster = (name) => {
    axios
      .delete(`https://mysql-animes.herokuapp.com/remove/${name}`)
      .then(() => {
        setPosters(posters.filter((poster) => poster.name !== name));
      });
  };

  useEffect(() => {
    fetch(`https://mysql-animes.herokuapp.com/animes`)
      .then((res) => res.json())
      .then((data) => setPosters(data.reverse()));
  }, []);

  return (
    <div>
      <Banner img="https://c.wallhere.com/photos/6c/bb/Little_Witch_Academia-284456.jpg!d" />

      <div className="input">
        <Input addPoster={addPoster} />
      </div>

      <div className="anime-wrapper">
        {posters.map((poster) => (
          <Poster
            name={poster.name}
            href={poster.href}
            img={poster.img}
            key={poster.name}
            deletePoster={deletePoster}
          />
        ))}
      </div>
    </div>
  );
}

export default Library;
